html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}


.page {
  height: 100%;
  font-family: $Montserrat;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
  font-size: calc(100vw/1200 * 10);
  @include tablet {
    font-size: calc(100vw/640 * 10);
  }

  @include mobile {
    font-size: calc(100vw/320 * 10);
  }


  &__body {
    margin: 0;
    min-width: rem(320);
    min-height: 100%;
    font-size: rem(14);
    background-color: $white;
    color: $blue;
    line-height: 1.2;
    font-weight: 500;
    overflow-x: hidden;
  }
}


.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}


button,
a {
  transition: color 0.2s, background-color 0.2s;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
  appearance: none;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  width: rem(1110);
  max-width: 100%;
  margin: 0 auto;
  padding: 0 rem(15);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  // height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}


@include tabs;
