.our-record {
  // clip-path: polygon(48% 0px, 80% 0px, 100% 0px, 100% 95.7%, 80.16% 98.57%, 52.86% 100.67%, 34.05% 99.87%, 14.11% 97.75%, 0 95.59%, 0px 0px, 22% 0px);
  padding: 3.1rem 0 7.3rem;
  position: relative;
  @include bg-cover;
  &__arrows {
    position: absolute;
    right: rem(-1);
    top: rem(28);
  }

  &__title {
    margin-bottom: rem(30);
  }

  &__slide {
    &:nth-child(even) {
      .our-record__text {
        align-self: end;
      }
    }
  }

  &__inner {
    position: relative;
    width: max-content;
    display: grid;
    &:hover {
      .our-record__liters {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__bg {
    grid-column: 1;
    grid-row: 1;
    width: rem(146);
    height: rem(151);
    border-radius: 50%;
  }

  &__text {
    margin: rem(10) 0;
    font-size: rem(10);
    grid-column: 1;
    grid-row: 1;
    text-align: center;
    color: rgba(252, 247, 232, 0.55);

  }

  &__liters {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(252, 247, 232, 0.77);
    font-size: rem(30);
    font-weight: 800;
    font-family: $NeueMachina;
    transition: opacity 0.2s, visibility 0.2s;
    padding-bottom: rem(32);
    &::before {
      position: absolute;
      content: '';
      bottom: rem(42);
      left: 0;
      right: 0;
      margin: 0 auto;
      @include bg-cover;
      background-image: url('../img/our-record/milk-img.png');
      width: rem(32);
      height: rem(32);
    }
  }
}
