.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  background-color: $white;
  &__nav {
    display: flex;
    align-items: center;
    padding-bottom: rem(1);
  }

  &__logo {
    margin-right: rem(37);
    transition: opacity 0.2s;
    &:hover {
      opacity: 0.8;
    }
  }

  .menu {
    display: flex;
    column-gap: rem(29);
    margin-top: -2rem;
    margin-right: rem(40);
    &__link {
      font-family: $NeueMachina;
      font-size: rem(15);
      letter-spacing: 0.025em;
      color: #00497E;
      &:hover {
        color: lighten($color: #00497E, $amount: 10)
      }
    }
  }

  &__tel {
    margin-top: -2rem;
    margin-right: rem(34);
    font-weight: 700;
    font-size: rem(16);
    color: #00497E;
    &:hover {
      color: lighten($color: #00497E, $amount: 10)
    }
  }
}
