.main-arrows {
  display: flex;
  column-gap: rem(11);

  &__arrow {
    width: rem(32);
    height: rem(32);
    background-color: #003C68;
    border-radius: 50%;
    position: relative;

    &:hover {
      background-color: lighten(#003C68, 10%);
    }

    &::before {
      content: '';
      position: absolute;
      inset: 0;
      margin: auto;
      @include bg-cover;
      background-image: url('../img/slider-arrow.svg');
      width: rem(9);
      height: rem(16);
    }

    &--right {
      &::before {
        transform: rotate(180deg) translateX(rem(-2));
      }
    }

    &--left {
      &::before {
        transform: translateX(rem(-1));
      }
    }
  }
}
