.about-numbers {
  @include bg-cover;
  clip-path: polygon(48% 0px, 80% 0px, 100% 0px, 100% 99.7%, 90.23% 97.41%, 79.93% 95.47%, 69.48% 94.21%, 59.62% 93.63%, 46.91% 93.52%, 35.42% 93.25%, 21.43% 95.21%, 11.36% 96.65%, 0px 99.7%, 0px 0px, 22% 0px);
  &__inner {
    max-width: rem(905);
    margin: 0 auto;
    padding: rem(58) 0 rem(100);
    display: flex;
    align-items: center;
    column-gap: rem(75)
  }

  &__img {
    width: rem(277);
    height: rem(208);
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: rem(43) rem(48);
  }

  &__num {
    // display: inline-block;
    font-size: rem(45);
    font-family: $NeueMachina;
    font-weight: 700;
    color: #003C68;
    // line-height: 0.91;
    margin-bottom: rem(5);
  }

  &__text {
    max-width: rem(182);
  }
}
