.hero-section {
  @include bg-cover;
  background-position-x: -3.4rem;

  &__container {
    width: 98.7rem;
  }

  &__inner {
    height: rem(620);
    display: flex;
    // align-items: center;
    padding-top: rem(160);
  }

  &__title {
    text-transform: uppercase;
    max-width: rem(627);
    font-size: rem(45);
    font-family: $NeueMachina;
    font-weight: 800;
    line-height: 0.92;
    letter-spacing: 0.022em;
    margin-bottom: rem(38);
  }

  &__subtitle {
    text-transform: none;
    font-family: $Montserrat;
    letter-spacing: 0.022em;
    padding-top: rem(14);
    display: block;
    font-size: rem(20);
    font-weight: 500;
  }

  &__text {
    margin-top: rem(-26);
    align-self: flex-start;
    line-height: 1;
    max-width: rem(380);
    font-size: rem(12);
    padding-left: rem(43);
    position: relative;
    &::before {
      position: absolute;
      content: '';
      left: rem(2);
      top: 0;
      width: rem(28);
      height: rem(28);
      @include bg-cover;
      background-image: url('../img/hero-section/icon-normal.svg');
    }

    p {
      letter-spacing: 0.028em;
      &:not(:last-child) {
        margin-bottom: rem(9);
      }
    }

    strong {

    }
  }
}
