.directions {
  padding-top: 0.5rem;
  transform: translateY(rem(-58));
  &__title {
    margin-bottom: rem(30);
  }

  .milk-production {

    &__inner {
      display: flex;
      column-gap: rem(50);
    }

    &__content {
      max-width: rem(596);
    }

    &__title {
      font-weight: 800;
      font-family: $NeueMachina;
      font-size: rem(25);
      line-height: 0.91;
      margin-bottom: rem(20);
    }

    &__text {
      margin-bottom: rem(27);
      p {
        &:not(:last-child) {
          margin-bottom: rem(20);
        }
      }
    }

    &__link {
      padding: 1.45rem 4.5rem 1.9rem 4.5rem;
      letter-spacing: normal;
    }

    &__img {
      transform: translateY(rem(27));
      width: rem(454);
      height: rem(303);
      border-radius: rem(305.5);
    }


  }
}
