@mixin tabs {
  .tabs {

    &__nav {
      border: 2px solid #003C68;
    }

    &__nav-item {
      flex-grow: 1;
      &:not(:last-child) {
        border-right: rem(2) solid #003C68;
      }
    }

    &__nav-btn {
      width: 100%;
      padding: rem(30) rem(60);
      letter-spacing: 0.035em;
      font-size: rem(18);
      color: #003C68;
      line-height: 0.94;
      font-family: $NeueMachina;
      &:hover, &--active {
        background-color: #003C68;
        color: #FDFDFD;
      }
    }

    &__content {
      padding: 6.1rem 3.3rem;
    }

    &__panel {
      display: none;

      &--active {
        display: block;
      }
    }
  }
}
